.content {
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.01);
    border-radius: 8px;
    background-color: white;
    padding: 50px 60px 72px;
    margin: 19px 0;
}

.content__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 38px;
}

.content__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #0C1014;
}

.content__buttons {
    display: flex;
    column-gap: 20px;
    align-items: center;
}

.content__set-btn {
    border: 2px solid #1890FF;
    border-radius: 50px;
    background-color: transparent;
    color: #1890FF;
    padding: 7px;
    transition: all 0.5s;
}

.content__set-btn:hover {
    background-color: #1890FF;
    color: #fff;
}

.content__all {
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #1890FF;
}

.content__cards-block {
    max-width: 918px;
    margin: 0 auto;
    display: flex;
    column-gap: 80px;
    flex-wrap: wrap;
}

.content__card {
    margin-top: 25px;
}

.content__card-img {
    width: 168px;
    height: 168px;
}

.content__card-title {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #0C1014;
    padding: 32px 0 4px 0;
}

.content__card-price {
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: #515759;
    padding-bottom: 12px;
}

.content__card-btn {
    width: 56px;
    height: 24px;
    background: #1890FF;
    border-radius: 4px;
    border: 0;
    font-weight: 500;
    font-size: 10px;
    line-height: 20px;
    color: #FFFFFF;
}

.content__delete-btn {
    background: transparent;
    color: #1890FF;
    border: 1px solid #1890FF;
    margin-left: 15px;
    transition: all 0.5s;
}

.content__delete-btn:hover {
    background: #1890FF;
    color: #FFFFFF;
}