* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a, a:hover {
    text-decoration: none;
}

.App {
    background: #E5E5E5;
    padding: 60px 0;
    min-height: 100vh;
    font-family: 'Inter', sans-serif;
}

.container {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 60px;
}

