.footer__navbar{
    padding: 30px 0 39px 67px;
    background-color: #FFFFFF;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.01);
    border-radius: 8px;
    display: flex;
    column-gap: 50px;
}
.footer__mail{
    font-size: 11px;
    line-height: 20px;
    color: #1890FF;
    margin-top: 8px;
}
.footer__title{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #0C1014;
}
.footer__subtitle{
    font-style: normal;
    font-size: 11px;
    line-height: 20px;
    color: #515759;
    margin-top: 8px;
}
.footer__number{
    font-weight: 600;
    font-size: 11px;
    line-height: 20px;
    color: #515759;
    margin-top: 16px;
}

.footer__icons {
    display: flex;
    column-gap: 27px;
    margin-top: 21px;
    color: #1890FF;
}