.open-popup {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #00000080;
    justify-content: center;
    align-items: center;
}

.open-popup__form {
    display: flex;
    flex-direction: column;
   justify-content: space-evenly;
    background: white;
    height: 350px;
    width: 300px;
    border-radius: 20px;
    padding: 20px;
    border: 3px solid #1890FF;
}

.open-popup__exit {
    cursor: pointer;
    width: 30px;
}

.open-popup__btn:hover{
    color: #1890FF;
}

.open-popup__input {
    border-radius: 10px;
    padding: 5px 10px;
    border: 2px solid #1890FF;
}

.open-popup__input::placeholder {
    color: black;
}

.open-popup__exit {
    position: relative;
    width: 30px;
    height: 30px;
    background: transparent;
    border: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.exit-line {
    width: 100%;
    height: 2px;
    background: black;
    display: block;
    transition: all .5s;
    position: absolute;
}

.exit-line:first-child {
    transform: rotate(45deg);
}

.exit-line:last-child {
    transform: rotate(-45deg);
}

.open-popup__btn {
    width: 85px;
    border: 1px solid #1890FF;
    background: transparent;
    color: #1890FF;
    border-radius: 10px;
    padding: 5px 10px;
    margin: 0 auto;
}