.header__nav {
    width: 100%;
    height: 60px;
    display: flex;
}

.header__logo {
    width: 20%;
    height: 100%;
    background: #FFFFFF;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.01);
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__links {
    width: 80%;
    height: 100%;
    background: #444444;
    border-radius: 0 8px 8px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 54px;
}

.header__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
}